import React, { useState, useRef, useEffect } from 'react';
import { 
    Alert,
    Grid,
    MenuItem,
    Box,
    CircularProgress,
    Snackbar
} from '@mui/material';
import FormControl from '@mui/material/FormControl'
import Button from '@mui/material/Button'
import { styled } from '@mui/system'
import { useNavigate } from 'react-router-dom'
import { ValidatorForm, TextValidator } from 'react-material-ui-form-validator';
import moment from 'moment';
import {
    createDevice
} from 'app/services/deviceService';
import { ToastContainer, toast } from 'react-toastify';
import useAuth from 'app/hooks/useAuth';
import 'react-toastify/dist/ReactToastify.css';
import './Devices.css';
import { SelectValidator } from 'react-material-ui-form-validator';
import SessionModal from 'app/components/SessionModal/SessionModal';

const TextField = styled(TextValidator)(() => ({
    width: '100%',
    marginBottom: '16px',
}))

const SelectField = styled(SelectValidator)(() => ({
    width: '100%',
    marginBottom: '16px',
    marginTop: '8px'
}))
const CancelButton = styled(Button)(() => ({
    display: 'block',
    color: '#fff',
    marginTop: '20px',
    width: '86px',
    backgroundColor: '#e15260',
    '&:hover': {
        backgroundColor: '#cc2333',
        color: '#fff'
    }
}))

const StyledProgress = styled(CircularProgress)(() => ({
    position: 'absolute',
    top: '6px',
    left: '25px',
}))

const adminStatusList = [
    {
        name: 'Available',
        value: 'Available'
    },
    {
        name: 'Repair',
        value: 'Repair'
    }
];

const centerStatusList = [
    ...adminStatusList,
    {
        name: 'Assigned',
        value: 'Assigned'
    },
    {
        name: 'Inventory',
        value: 'Inventory'
    }
]

const AddDevice = (props) => {
    const navigate = useNavigate();
    const { user } = useAuth();
    const [deviceInfo, setDeviceInfo] = useState({
        device_type: '',
        device_serial: '',
        mac_address: '',
        status: ''
    });
    const [loading, setLoading] = useState(false);
    const [open, setOpen] = useState(false);
    const [statusInfo, setStatusInfo] = useState({
        status: '',
        messages: []
    });
    const [openModal, setOpenModal] = useState(false);
    const [msg, setMsg] = useState('');
    const formEl = useRef();

    useEffect(() => {
        ValidatorForm.addValidationRule('REQUIRED', (value) => {
            if(value.length === 0) return false;
            return true;
        })

        return () => {
            ValidatorForm.removeValidationRule('REQUIRED');
        }
    },[]);

    function handleChange(event) {
        event.persist();
        setDeviceInfo({
            ...deviceInfo,
            [event.target.name]: event.target.value
        })
    }

    const handleSubmit = (event) => {
        setLoading(true);
        const {
            device_type,
            device_serial,
            mac_address,
            status,
        } = deviceInfo;

        let options = {
            data: {
                device_type,
                device_serial,
                mac_address,
                status,
                is_assigned: false,
                publishedAt: moment().format().split('T')[0] + ' ' + moment().format().split('T')[1].slice(0,8)
            }
        };

        if(user && user.role && user.role.type === 'admin'){
            options.data.owned_by = 'bmc';
        }

        createDevice(options)
        .then(resp => {
            if(resp?.error?.name === 'UnauthorizedError'){
                setOpenModal(true);
                setMsg('Your session has expired. You will be logged out.');
            } else {
                if(resp && resp.status === 'success') {
                    setOpen(true);
                    setStatusInfo({
                        ...statusInfo,
                        status: 'success',
                        messages: [resp.message]
                    })
                } else if(resp && resp.status === 'fail') {
                    let arr = [];
                    resp.errors.forEach(el => {
                        arr.push(el.message);
                    })
                    setOpen(true);
                    setStatusInfo({
                        ...statusInfo,
                        status: 'error',
                        messages: arr
                    });
                } else {
                    setLoading(false);
                    setOpen(true);
                    setStatusInfo({
                        ...statusInfo,
                        status: 'error',
                        messages: ['Something went wrong']
                    })
                }
            }
        })
    }

    const {
        device_type,
        device_serial,
        mac_address,
        status,
    } = deviceInfo;

    return (
        <div className="device-card">
            <ValidatorForm
                ref={formEl}
                onSubmit={handleSubmit}
                onError={() => null}
            >
                <Grid container rowSpacing={2} columnSpacing={6}>
                    <Grid item lg={6} md={6} sm={12} xs={12}>
                        <FormControl fullWidth>
                            <SelectField
                                id="demo-simple-select"
                                value={device_type}
                                label="Select Device Type*"
                                onChange={(e) =>
                                    setDeviceInfo({
                                        ...deviceInfo,
                                        device_type: e.target.value,
                                    })
                                }
                                name="device_type"
                                validators={['REQUIRED']}
                                errorMessages={['This field is required']}
                            >
                                <MenuItem value="">Select Device Type</MenuItem>
                                <MenuItem value="iPad">iPad</MenuItem>
                                <MenuItem value="iPhone">iPhone</MenuItem>
                                <MenuItem value="Android Phone">
                                    Android Phone
                                </MenuItem>
                                <MenuItem value="Android Tablet">
                                    Android Tablet
                                </MenuItem>
                            </SelectField>
                        </FormControl>
                    </Grid>
                    <Grid item lg={6} md={6} sm={12} xs={12}>
                        <TextField
                            margin="dense"
                            id="device_serial"
                            name="device_serial"
                            label="Device Serial*"
                            type="text"
                            value={device_serial}
                            onChange={handleChange}
                            fullWidth
                            validators={['REQUIRED']}
                            errorMessages={['This field is required']}
                        />
                    </Grid>
                </Grid>

                <Grid container rowSpacing={2} columnSpacing={6}>
                    <Grid item lg={6} md={6} sm={12} xs={12}>
                        <TextField
                            margin="dense"
                            id="mac_address"
                            name="mac_address"
                            label="Mac Address*"
                            type="text"
                            value={mac_address}
                            onChange={handleChange}
                            fullWidth
                            validators={['REQUIRED']}
                            errorMessages={['This field is required']}
                        />
                    </Grid>
                    <Grid item lg={6} md={6} sm={12} xs={12}>
                        <FormControl fullWidth>
                            <SelectField
                                id="demo-simple-select"
                                value={status}
                                label="Select Status*"
                                onChange={(e) =>
                                    setDeviceInfo({
                                        ...deviceInfo,
                                        status: e.target.value,
                                    })
                                }
                                name="status"
                                validators={['REQUIRED']}
                                errorMessages={['This field is required']}
                            >
                                <MenuItem value="">Select Status</MenuItem>
                            {user &&
                                user.role &&
                                user.role.type === 'admin' &&
                                adminStatusList &&
                                adminStatusList.map((item, i) => (
                                    <MenuItem key={i} value={item.value}>
                                        {item.name}
                                    </MenuItem>
                                ))}
                            {user &&
                                user.role &&
                                user.role.type === 'center' &&
                                centerStatusList &&
                                centerStatusList.map((item, i) => (
                                    <MenuItem key={i} value={item.value}>
                                        {item.name}
                                    </MenuItem>
                                ))}
                            </SelectField>
                        </FormControl>
                    </Grid>
                </Grid>

                <div
                    style={{
                        display: 'flex',
                        alignItems: 'center',
                        gap: '1rem',
                    }}
                >
                    {/* <Button sx={{ display: 'block', marginTop: '20px'}} color="primary" variant="contained" type="submit">
                        <Span sx={{ pl: 1, textTransform: 'capitalize' }}>
                            Submit
                        </Span>
                    </Button> */}
                    <Box
                        position="relative"
                        sx={{ display: 'block', marginTop: '20px' }}
                    >
                        <Button
                            disabled={loading}
                            color="primary"
                            variant="contained"
                            type="submit"
                        >
                            Submit
                        </Button>
                        {loading && (
                            <StyledProgress
                                size={24}
                                className="buttonProgress"
                            />
                        )}
                    </Box>
                    <CancelButton onClick={() => navigate(-1)}>
                        Cancel
                    </CancelButton>
                </div>
            </ValidatorForm>
            <Snackbar
                open={open}
                anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
                autoHideDuration={3000}
                onClose={() => {
                    setLoading(false);
                    setOpen(false)
                    if(statusInfo && statusInfo.status === 'success'){
                        navigate('/devices');
                    }
                }}
            >
                <div>
                {statusInfo && statusInfo.messages.map((el) => (    
                    <Alert
                        onClose={() => {
                            setLoading(false);
                            setOpen(false);
                            if(statusInfo && statusInfo.status === 'success'){
                                navigate('/devices');
                            }
                        }}
                        variant="filled"
                        severity={statusInfo.status}
                        sx={{ width: '100%', marginBottom: '10px' }}
                    >
                        {el}
                    </Alert>
                ))}
                </div>
            </Snackbar>
            <SessionModal
                open={openModal}
                text={msg}
                onConfirmDialogClose={() => {
                    setMsg('');
                    setOpenModal(false);
                }}
                onYesClick={() => {
                    setMsg('');
                    setOpenModal(false);
                }}
            />
        </div>
    )
}

export default AddDevice;