import { makeRequest } from './authenticationService';

export const fetchDevices = (page, value) => {
    let url = `/api/devices?pagination[page]=${page}&tab=${value}`;

    return makeRequest('GET', url)
        .then((resp) => {
            return resp;
        })
        .then((json) => {
            return json;
        }); 
}


