export const findDevices = (options) => {
    let searchData = options && options.search || '';
    let filterData = options && options.type || null;
    let page = options && options.page || 1;
    let tab = options && options.activeTab || 1;
    let centerName = options && options.centerName;

    let url = `/api/devices?search=${searchData}&tab=${tab}&pagination[page]=${page}`;

    if(filterData) {
        for(const [key, value] of Object.entries(filterData)) {
            url += `&filter[${key}]=${value}`;
        }
    }

    if(centerName){
        url += `&centerName=${centerName}`;
    }

    return makeRequest('GET', url)
        .then((resp) =>{
            return resp;
        })
        .catch((err)=>{
            return err;
        })
}


export const fetchDeviceUsage = (page, deviceId) => {

    let url = `/api/device-usages?pagination[page]=${page}`;
    
    if(deviceId) {
        url += `&deviceId=${deviceId}`;
    }

    return makeRequest('GET',url)
        .then((resp) =>{
            return resp;
        })
        .catch((err)=>{
            return err;
        })
}

export const findDeviceUsage = (options) => {
    const page = options?.page || 1;
    const search = options?.search || '';
    const filter = options?.type || 'All';
    const deviceId = options?.deviceId;

    let url = `/api/device-usages?pagination[page]=${page}`;

    if(search){
        url += `&search=${search}`;
    }
    if(filter && filter !== 'All') {
        url += `&filter=${filter}`;
    }
    if(deviceId) {
        url += `&deviceId=${deviceId}`;
    }

    return makeRequest('GET', url)
        .then((resp) =>{
            return resp;
        })
        .catch((err)=>{
            return err;
        })
}

